    background-color: #2f435a;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
    outline: 0;
    border: none;
    border-radius: 3.125em;
    color: #fdce6d;
    font-family: poppinssemibold, sans-serif;
    font-size: 1.1em;
    transition: 0.5s;
    cursor: pointer;
  }

  #agendar:hover {
    display: flex;
    background-color: #fdce6d;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
    outline: 0;
    border: none;
    border-radius: 3.125em;
    color: #2f435a;
    font-family: poppinssemibold, sans-serif;
    transition: 0.5s;
    cursor: pointer;
  }

/* FIM BOTÕES */



@media screen and (max-width: 1024px) {
  body {
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    width: 700px;
  }

}


@media screen and (max-width: 575px) {
  body {
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    width: 575px;
  }

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 95%;
    margin-right: auto;
    margin-left: auto;
    margin-top: 2.5em;
    position: relative;
  }

  .menu-icon-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 43px;
    width: 80px;
    cursor: pointer;
    border-radius: 2px;
  }

  .fa-arrow-left {
    margin-top: 2px;
    width: 80px;
    font-weight: 600;
    font-size: 40px;
    color: #2f435a;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
  }

  .fa-arrow-left:hover {
    font-weight: 600;
    color: #fdce6d;
    transition: 0.2s all;
  }

  .menu-icon-container>button {
    background-color: transparent;
    width: 80px;
      height: 43px;
    outline: 0;
    border: none;
    cursor: pointer;
  }

  .title-principal {
    display: none;
  }

  .title-pagina {
    display: block;
    width: max-content;
    align-self: center;
    justify-self: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    color: #2f435a;
    font-family: poppinsmedium, sans-serif;
    font-size: 24px;
  }

  .logo {
    display: flex;
    position: absolute;
    left: 42%;
    border: none;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));
  }

  .text-idade, .text-porte, 
  .text-pelagem, .text-raca {
    display: flex;
    width: 260px;
    align-items: center;
    height: 1.5625em;
    padding: 0.3125em;
    gap: 5px;
    background-color: #2f435a;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
    border-radius: 0.5em;

    color: #fdce6d;
    font-family: poppinssemibold, sans-serif;
    font-size: 1.125em;
  }

}


@media screen and (max-width: 480px) {
  body {
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    width: 480px;
  }

  .pet-properties-um, 
  .pet-properties-dois {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 10px;
    width: 100%;
    gap: 5px;
  }

  .text-idade, .text-porte, 
  .text-pelagem, .text-raca {
    display: flex;
    width: 447px;
    align-items: center;
    height: 1.875em;
    padding: 0.3125em;
    gap: 5px;
    background-color: #2f435a;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
    border-radius: 0.5em;

    color: #fdce6d;
    font-family: poppinssemibold, sans-serif;
    font-size: 1.125em;
  }

  #idade, #porte, 
  #pelagem, #raca {
    width: 100%;
    height: 1.875em;
    margin-top: 0.3125em;
    margin-bottom: 10px;
    padding: 0 0.3125em;
    align-items: center;
    outline: 0;
    border: none;
    border-radius: 0.5em;
    font-family: poppinsmedium, sans-serif;
    font-size: 1em;
    line-height: 1.875em;
    box-sizing: border-box;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
    background-color: #fff1d4;
    color: #2f435a;
  }

  #servicos label {
    display: flex;
    margin-right: auto;
    margin-left: auto;
    margin-top: 1em;
    padding-left: 10px;
    width: 100%;
    height: 30px;
    border-radius: 3.125em;
    align-items: center;
    background-color: #2f435a;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  }
}
